import Vue from 'vue';
import Vuex from 'vuex';//vuexnpm install vuex --save
Vue.use(Vuex);// Vue.use(Vuex)  Vuex

 const state={//state
     loginFresh: false	//
    };

 
const getters = {   //state()
    getLoginFresh(state) {  //,showFooter
       return state.loginFresh
    }
};

const mutations = {
    needLoginFresh(state) {   //statestate();
        state.loginFresh = true;
    },
    unneedLoginFresh(state) {  //
        state.loginFresh = false;
    },
    setLoginFresh(state, login){
        state.loginFresh = login;
    }
};

 const actions = {
    needLoginFresh(context) {  //mutationscontextstore 
        context.commit('needLoginFresh');
    },
    unneedLoginFresh(context) {  //
        context.commit('unneedLoginFresh');
    },
    setLoginFresh(state, loginFresh){
         context.commit('setLoginFresh',loginFresh);
    }
};

const store = new Vuex.Store({
    state,
    getters,
    mutations,
    actions   
});

export default store;	


