import Vue from 'vue'
import App from './App'

//npm install vue-router
//https://router.vuejs.org/zh/installation.html
import router from './router'

//Vuex  Vue.js 
//vuexhttps://vuex.vuejs.org/zh/
import vuex from 'vuex' //vuexnpm install vuex --save

//axioshttpnpm install axios      
//https://www.jianshu.com/p/7a9fbcbb1114
//https://github.com/axios/axios
//https://cn.vuejs.org/v2/cookbook/using-axios-to-consume-apis.html
import axios from 'axios'  //axios  
import './css/1.css' //css
import store from './store'//store

// base.vue
import global_ from './components/base.vue'
Vue.prototype.GLOBAL = global_;
//axios
axios.defaults.baseURL=global_.BASE_URL;
//session
axios.defaults.withCredentials=true;
//axiosvuethis.$httpaxios
Vue.prototype.$http = axios  

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,//
  store,//store
  components: { App },
  template: '<App/>'
})



