<template>
    <div id="home"> <!-- template  -->       
        <div class="news">
            <!-- v-for  newsTypesdata   index   -->
            <template v-for="(newsType,index) in newsTypes">
                <div :class="{ newsleft:index%2==0, newsRight:index%2!=0}"><!-- div  -->
                    <table class="invisibleTable">
                        <tbody>
                            <tr class="newsColumn"><!--  -->
                                <td>
                                    <!-- v-if  -->
                                    <template  v-if="newsType === 'all'"> 
                                         
                                    </template >
                                    <template  v-else> 
                                        {{newsType}}   <!--   -->
                                    </template >                                
                                </td>
                                    
                                <td class="right">
                                    <!--. :hrefhrefvue   -->
                                    <a :href="'/news2/servlet/NewsServlet?type1=showNewsByNewsType&newsType='+newsType+'&page=1&pageSize=10'"></a>
                                </td>
                            </tr>
                        <template v-for="news in newsesList[index]">
                            <tr>
                                <td class="mainPageUl">
                                    <a :href="news.url" :title="news.caption">
                                        {{news.caption}}
                                    </a>
                                </td>
                                <td class="right">
                                    <!--   method -->
                                    {{getYearMonthDay(news.newsTime)}}
                                </td>
                            </tr>
                        </template >
                        </tbody>
                    </table>
                </div>              
            </template>            
        </div>        
    </div>
</template> 

<script type="text/javascript">

   // export default 
export default {
    name: 'home',    

data () {  //
return {
            newsTypesNumber:0,
            newsTypes: [],
            newsesList:[]
}
},

    //vuehttps://blog.csdn.net/qq_35585701/article/details/81216704
    //createdhtml
    created:function(){  
        this.getNewsHeads();
    },
    methods:{//
        getNewsHeads:function(){                    
            var url ='/servlet/NewsServlet?type1=homepageTypes2';     
            //axiosget jsonresponse.data
            this.$http.get(url)  //
                .then(response =>{
                    // 
                    this.newsTypesNumber=response.data[0];
                    this.newsTypes=response.data[1];
                    this.newsesList=response.data[2];   
                    console.log(response.data);                                    
                }).catch(error => {
                    console.log(err);
                });
        },
        getYearMonthDay: function( date ){
            if(date!=null)
                return date.date.year+"-"
                    +(date.date.month>9?date.date.month:"0"+date.date.month)+"-"
                    +(date.date.day>9?date.date.day:"0"+date.date.day);
            else
                return "";
        }
    }
}
</script>
<style scoped></style>


