<template>
	<div id="login">
		<form id="myform">
			<div class="center loginDiv">
				<table  height="121" border="0" align="center">
					<tbody>
						<tr height="30">
							<td></td><td></td>
						</tr>			
						<tr height="30">
							<td align="right"></td>
                            <!-- v-modellogin.name   -->
							<td align="left"><input type="text" v-model="login.name"></td>
						</tr>
						<tr height="30">
							<td align="right"></td>
                            <!-- v-modellogin.password   -->
							<td align="left"><input type="password" v-model="login.password"></td>
						</tr>
					    <tr>
					      <td align="right"></td>
					      <td valign="middle">
                                <!-- v-modellogin.checkCode   -->
                                <input type="text" v-model="login.checkCode" required="required">
                                <!-- this.GLOBAL.BASE_URL base.vueGLOBAL.BASE_URL   -->
					      		<img id="checkImg"   :src="this.GLOBAL.BASE_URL+'/servlet/ImageCheckCodeServlet?rand=-1'" class="hand" /></td>
					    </tr>					
						<tr height="30">
                            <!-- v-on:click   -->
							<td></td><td><input v-on:click="submitForm" type="button"  value="          "/>
							&nbsp; &nbsp; &nbsp; &nbsp; <a href="findPassword.jsp"></a></td>
						</tr> 										
					</tbody>
				</table>
			</div>
		</form>
	</div>
</template>

<script type="text/javascript">
    //qsnpmnpm install qs
    //qs
    import Qs from 'qs'; 

    export default {
        name: 'login',

    data () {  //
    return {
                login:{//login
                    name: '',
                    password: '',
                    checkCode: ''
                }
    }
    },
        methods:{
            submitForm:function(){ 
                console.log(this.login);
                //qs.stringify() 'name=hehe&age=10'
                var data = Qs.stringify(this.login);
                console.log(data)

                var url ='/servlet/UserServlet?type1=login'     
                //axiosajaxpost
                this.$http.post(url,data,
                     {headers:{'Content-Type':'application/x-www-form-urlencoded'}})  //Content-Type'application/x-www-form-urlencoded'
                    .then(response =>{
                        // 
                        if(response.data.result==1){
                            //1.headPage.vue2home.vue
                            console.log(response.data);
                            console.log( "aaaaaa"+this.$store.getters.getLoginFresh);
                            //storeloginFreshtrueheadPage
                            this.$store.dispatch('needLoginFresh');
                            console.log( "bbbbbb"+this.$store.getters.getLoginFresh);
                            //App.vue<router-view/>home.vue
                            this.$router.push({path:'/'}); 
                        }
                        else//                      
                            alert(data.message);                                
                    }).catch(error => {
                        console.log(err);
                    }
                );
            }
        }
    }
</script>	


<style type="text/css">
	
    .loginDiv {
       width:600px;
       margin-top:40px
    }
</style>


