<template>
  <div id="app"><!-- template  -->
    <!--@go_homeheadPagego_homegohomego_home  -->
    <headPage @go_home="gohome"></headPage>
    <router-view/>
    <tailPage></tailPage>
  </div>
</template>

<script>
    //App.vue 
    //
    import headPage from './components/headPage.vue'
    import tailPage from './components/tailPage.vue'  
    export default {
      name: 'App',
      // components  template 
      components: { 
        tailPage ,  
        headPage 
      }, 
      methods:{ //
        gohome(data){//
          this.$router.push({ path: '/' });
        }
      }
    }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>



