<template>
	<div id="headPage">
	  <form id="searchForm" action="" method="post">
		 <div class="div-out">
		 	<div class="logleft">
		 		<a href="/news2/index.jsp">
                <!-- assets..   -->
                <img src="../assets/images/log.png"></a>
		 	</div>
		 	<div class="logMiddle">
				<div class="logMiddleInner">		
					<input type="text" id="search" name="search">
                     <!-- assets..   -->
					<img class="imagePointer" src="../assets/images/search_b.jpg" onclick="search();" >
				</div> 	
		 	</div>
			<div class="logRight">
				<div class="logRightInner">	
                    <template  v-if=" user ">
                         <!-- router-linka /manageMainrouter/index.js  -->
                        <router-link to="/manageMain"></router-link>
                    </template >					
				
					<a href="/news2/index.jsp"></a>&nbsp;

                    <template  v-if=" !user "> 
                        <!-- router-linka /manageMainrouter/index.js  -->
                        <router-link to="/login"></router-link>
							&nbsp;<a href="/news2/user/public/register.jsp"></a>
                    </template >
                    <template  v-else> 
                        {{user.name}}&nbsp;
                        <!-- @clickclicklogoutmethod  -->
                        <a @click="logout" href="#"></a>
                    </template >   
	
				</div> 	
		 	</div>
		</div>
		<div class="clear"></div>
	</form>	   

    </div>
</template>

<script type="text/javascript">
    //vuex
    import {mapState,mapMutations,mapGetters} from 'vuex';

    export default {
        name: 'headPage',

    data () {  //
    return {
                user: {}
    }
    },
        created:function(){  //
            this.getUser();
        },
        methods:{
            getUser:function(){                    
                var url ='/servlet/UserServlet?type1=user'     
                //axiosajaxget
                this.$http.get(url)  //
                    .then(response =>{
                        // 
                        this.user=response.data;   
                        // console.log(this.user);                                   
                    }).catch(error => {
                        console.log(err);
                    });
            },
            logout:function(){
                var url ='/servlet/UserServlet?type1=exit';
                this.$http.get(url)  //
                    .then(response =>{
                        // 
                        if(response.data.result===1){
                            this.user=null;//null
                            //go_home1 
                            this.$emit('go_home',1);                    
                        }                                  
                    }).catch(error => {
                        console.log(error);
                    });            

            }
        } ,
        computed: {
            //storeloginFresh
            ...mapGetters([
                  'getLoginFresh'
                ])
        },
        watch: {
            //storeloginFresh getLoginFresh(curval,oldval)
            getLoginFresh(curval,oldval) {
                console.log( "ccccccc"+curval);  
                //vuexloginFreshlogin.vuethis.$store.dispatch('needLoginFresh');
                if(curval==true){
                    this.getUser();
                    //$store.getters.getLoginFresh  vuexloginFreshh
                    console.log( "dddddd"+this.$store.getters.getLoginFresh);  
                     //vuexloginFresh
                    this.$store.dispatch('unneedLoginFresh');
                     console.log( "eeeeee"+this.$store.getters.getLoginFresh);  
                }
            }
        }
    }

</script>
<style scoped>
    .cursor:pointer{
        cursor:pointer;
        width:20px;
        height: 20px
    }
    
</style>


