<template>
    <!-- ref="frameDiv"  vueref   -->
    <div class="newsShowByType_frame center"  id="frameDiv" ref="frameDiv">
        <!-- ref="leftDiv"  vueref   -->
        <div class="newsShowByType_left" id="leftDiv" ref="leftDiv">
            <ul>   
                <!-- @click  -->         
                <li @click="goFunc(1)"></li>     
                <li @click="goFunc(2)"></li>     
                <li @click="goFunc(3)"></li>
            <template  v-if="  this.user.type=='manager' "> 
                <br>
                <li @click="goFunc(4)"></li>
                <li @click="goFunc(5)"></li>
                <li @click="goFunc(6)"></li>
                <li @click="goFunc(7)"></li>
                <li @click="goFunc(8)"></li>
                <li @click="goFunc(9)"></li>
                <br>
                <li @click="goFunc(10)"></li>  
                <li @click="goFunc(11)"></li> 
                <li @click="goFunc(12)"></li>
                <li @click="goFunc(13)">html</li>
                <br>
                <li @click="goFunc(14)"></li>
                <li @click="goFunc(15)"></li>
            </template >                                             
            <template  v-if=" this.user.type=='newsAuthor' "> 
                <br>
                <li @click="goFunc(16)"></li>
                <li @click="goFunc(17)"></li>
            </template >                 
            </ul>
        </div>  
        <div class="manageMain_right" id="rightDiv">
             <!--  App.vue<router-view></router-view>  -->
            <router-view></router-view>
        </div>
        <div class="clear"></div>
      </div>
</template>

<script type="text/javascript">
export default {
    name: 'manageMain',

data () {  //
return {
            user:""
}
},
    created:function(){  //vuemounted
        this.getUser();
        console.log(user);
    }, 
    //mountedjqueryreadyhtmlhtmldomcreated
    mounted(){
            // this.$refs.frameDiv: ref frameDiv
           this.$refs.frameDiv.style.height=this.$refs.leftDiv.offsetHeight+"px";
    },   
    methods:{ 
        getUser:function(){                    
            var url ='/servlet/UserServlet?type1=user'     
            //axiosajaxget
            this.$http.get(url)  //
                .then(response =>{
                    // 
                    this.user=response.data;   
                     console.log(this.user);                                   
                }).catch(error => {
                    console.log(err);
                });
        },        
        goFunc:function(i){
            console.log(i);
            switch(i){
                case 1:break;
                case 2:
                    //changePrivate<router-view></router-view>
                    this.$router.push({ name: 'changePrivate' });
                    break;
            }
        }
    }
}

</script>

<style type="text/css">	
    .newsShowByType_left li:hover{
       color:red
    }
    .newsShowByType_left ul{
        list-style-type:none
    }
</style>


