import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'  //@src
import login from '@/components/user/public/login'
import manageMain from '@/components/user/manageUIMain/manageMain'
import changePrivate from '@/components/user/manage/changePrivate'


// Vue.use() 
//import Router from 'vue-router'
//Vue.use(Router) 
Vue.use(Router)  

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/manageMain',
      name: 'manageMain',
      component: manageMain,
        //childrenmanageMain<router-view></router-view>App<router-view></router-view>
        children: [
          {
            path: 'changePrivate',
            name: 'changePrivate',
            component: changePrivate
          }
        ]      
    }        
  ]
})



